* {
  box-sizing: border-box;
}

body {
  margin: 0;
  min-height: 100vh;
  display: grid;
  place-items: center;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  background-color: #4c729a;
}

.app {
  width: 100%;
  max-width: 200px;
  display: grid;
  row-gap: 15px;
  grid-auto-flow: row;
  place-items: center;
  padding: 20px;
  background-color: white;
  border-radius: 4px;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.08);
  border: 1px solid #dedede;
}

.checkbox {
  display: inline-flex;
  cursor: pointer;
}
.checkbox__item {
  position: absolute;
  margin: -1px;
  padding: 0;
  width: 1px;
  height: 1px;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.checkbox__handler {
  display: block;
  width: 55px;
  height: 30px;
  background-color: #eee;
  border: 2px solid #eee;
  border-radius: 15px;
  transition-property: background-color, border-color;
  transition-duration: 150ms;
}
.checkbox__icon {
  display: block;
  width: 26px;
  height: 26px;
  background-color: white;
  border-radius: 13px;
  box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.1);
  transition: transform 200ms ease;
}
.checkbox__item:focus ~ .checkbox__handler, .checkbox__item:checked ~ .checkbox__handler {
  border-color: #5dc88c;
}
.checkbox__item:checked ~ .checkbox__handler {
  background-color: #5dc88c;
}
.checkbox__item:checked ~ .checkbox__handler .checkbox__icon {
  transform: translateX(25px);
}